<template>
  <div>
    <el-container>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>Register 注册比赛</span>
        </div>
        <div class="form-container">
          <Form
            :model="form"
            :rules="rules"
            class="form-body"
            label-width="100px"
            ref="form"
          >
            <FormItem label="用户名" prop="username">
              <ElInput
                v-model="form.username"
                placeholder="User Name 5-15字符"
              ></ElInput>
            </FormItem>
            <FormItem label="真实姓名" prop="fullname">
              <ElInput
                v-model="form.fullname"
                placeholder="Full Name 2-15字符"
              ></ElInput>
            </FormItem>
            <FormItem label="队伍名称" prop="teamname">
              <ElInput
                v-model="form.teamname"
                placeholder="Team Name 2-15字符"
              ></ElInput>
            </FormItem>
            <FormItem label="学校" prop="school">
              <ElInput
                v-model="form.school"
                placeholder="School 4-15字符"
              ></ElInput>
            </FormItem>
            <FormItem label="密码" prop="password">
              <ElInput
                type="password"
                v-model="form.password"
                placeholder="Password 4-15字符"
                autocomplete="off"
              ></ElInput>
            </FormItem>
            <FormItem label="重复密码" prop="checkPass">
              <ElInput
                type="password"
                v-model="form.checkPass"
                autocomplete="off"
                placeholder="Re-enter Password"
              ></ElInput>
            </FormItem>
            <FormItem class="button-group">
              <el-button type="primary" @click="submitForm('form')" disabled>
                注册
              </el-button>
              <el-button @click="resetForm('form')">重置</el-button>
            </FormItem>
          </Form>
          <el-card class="form-notice">
            <div slot="header">
              <span>提示</span>
            </div>
            <p style="color: #dd4444;font-size:1.2rem;line-height:2rem">
              暂停注册功能
            </p>
            <p>
              提交代码时，为避免异常，请将#include后的空格删除。
            </p>
            <p>
              如果有提交异常问题，请加入QQ群1070320286垂询。
            </p>
            <p>
              如果有其他建议或问题，请发邮件至 QLUICPC@163.com。
            </p>
          </el-card>
        </div>
      </el-card>
    </el-container>
  </div>
</template>

<script>
import { Form, FormItem, Input } from "element-ui";
export default {
  components: {
    Form,
    FormItem,
    ElInput: Input
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.form.checkPass !== "") {
          this.$refs.form.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.form.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      form: {
        username: "",
        fullname: "",
        teamname: "",
        school: "",
        password: "",
        checkPass: ""
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 5, max: 15, message: "长度在5到15个字符", trigger: "blur" }
        ],
        fullname: [
          { required: true, message: "请输入真实姓名", trigger: "blur" },
          { min: 2, max: 15, message: "长度在5到15个字符", trigger: "blur" }
        ],
        teamname: [
          { required: true, message: "请输入队伍名称", trigger: "blur" },
          { min: 2, max: 15, message: "长度在2到15个字符", trigger: "blur" }
        ],
        school: [
          { required: true, message: "请输入学校", trigger: "blur" },
          { min: 4, max: 25, message: "长度在4到25个字符", trigger: "blur" }
        ],
        password: [
          { required: true, validator: validatePass, trigger: "blur" },
          { min: 4, max: 25, message: "长度在4到25个字符", trigger: "blur" }
        ],
        checkPass: [
          { required: true, validator: validatePass2, trigger: "blur" },
          { min: 4, max: 25, message: "长度在4到25个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submitForm: function(arg) {
      this.$refs[arg].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(arg) {
      this.$refs[arg].resetFields();
    }
  }
};
</script>

<style scoped>
.box-card {
  width: 100%;
  margin: 20px;
}

.form-body {
  width: 450px;
}

.form-container {
  display: flex;
  flex-direction: row;
}

.button-group {
  float: right;
}

.form-notice {
  margin: 0 0 0 40px;
  width: 300px;
  background: rgb(255, 253, 248);
}

.form-notice p {
  margin: 5px 0 5px 0;
  line-height: 1.6rem;
}

@media (max-width: 640px) {
  .form-body {
    width: 100%;
  }
}

@media (max-width: 800px) {
  .form-container {
    flex-direction: column-reverse;
  }

  .form-notice {
    margin: 0 0 25px 0;
    width: 100%;
  }
}
</style>
